@charset "UTF-8";
*::-webkit-input-placeholder {
  color: #202020;
  opacity: 1;
}

*::-moz-placeholder {
  color: #202020;
  opacity: 1;
}

*::-ms-input-placeholder {
  color: #202020;
  opacity: 1;
}

::placeholder {
  color: #202020;
}

::selection {
  background-color: orange;
  color: #fff;
}

/* font-family: "OpenSansRegular"; */
@font-face {
  font-family: "OpenSansRegular";
  src: url("../fonts/OpenSansRegular/OpenSansRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/OpenSansRegular/OpenSansRegular.woff") format("woff"), url("../fonts/OpenSansRegular/OpenSansRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
  font-display: fallback;
}

@font-face {
  font-family: "RobotoCondensedBold";
  src: url("../fonts/OpenSansRegular/OpenSansRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/OpenSansRegular/OpenSansRegular.woff") format("woff"), url("../fonts/OpenSansRegular/OpenSansRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "OpenSansRegular", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.header_fon {
  background-color: red;
  min-height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
}

header {
  text-align: center;
  color: #fff;
}

.block {
  padding: 50px 0;
}

.header_fon h1 {
  color: gray;
}

.in_hea {
  color: orange;
}

.iconic_box {
  border: 1px solid #000;
}

